import Vue from 'vue'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import router from './router'
import store from './store'
import { post, get}  from "../config/axios";

Vue.use(ElementUI)

Vue.prototype.$httpGet = get;
Vue.prototype.$httpPost = post;

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
    if(sessionStorage.getItem('curUser')){ //判断本地是否用户信息
      next();
    }else {
      next({ path:'/login'})
      //拿token换登录态
      // get("user/cur").then(response => {
      //     if(response.code === 0) {
      //       sessionStorage.setItem('curUser', JSON.stringify(response.data))
      //       next()
      //     } else {
      //       next({ path:'/login'})
      //     }
      // })
    }
  } else {
    next();
  }
});



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
