import Vue from 'vue'
import Vuex from 'vuex'
import VueCookies from 'vue-cookies'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    curUser: sessionStorage.getItem('curUser')?JSON.parse(sessionStorage.getItem('curUser')):null
  },
  mutations: {
    login:(state, retData) => {
        VueCookies.set("token", retData.token)
        sessionStorage.setItem('curUser', JSON.stringify(retData.userInfo))
    },
    logout:() => {
        VueCookies.remove("token")
        sessionStorage.removeItem('curUser')
    },
  },

})
